<template>
  <div class="goods-item" @click="itemClick">
      <img v-lazy="books.cover_url" alt="">
      <div class="goods-info">
          <p>{{ books.title }}</p>
          <span class="price"><small>￥</small>{{ books.price }}</span>
          <span class="collect">{{ books.collects_count }}</span>
      </div>
  </div>
</template>

<script>
import {useRouter} from 'vue-router'
export default {
    name:'GoodsListItem',
    props: {
        books: Object,
        default() {
            return {}
        }
    },
    setup(props) {
        const router = useRouter();

        return {
            itemClick:() => {
                router.push({path:'/detail', query:{id:props.books.id}})
            }
        }
    }
}
</script>

<style scoped lang="scss">
.goods-item {
    width: 49%;
    padding-bottom: 35px;
    position: relative;
    background: #fff;
    margin-bottom: 6px;
    border-radius: 6px;
    box-shadow: 0 0.4px 0.8px #ccc;

    img {
        width: 100%;
        border-radius: 6px;
    }

    .goods-info {
        font-size: 12px;
        position: absolute;
        bottom: 5px;
        left: 0;
        right: 0;
        overflow: hidden;
        text-align: center;

        p {
            overflow: hidden;
            // 文字超出部分省略 ...
            text-overflow: ellipsis;
            //空白部分不交换
            white-space: nowrap;
            margin-bottom: 3px;

        }

        .price {
            color: red;
            margin-right: 30px;
        }

        .collect {
            position: relative;
        }
        .collect::before {
            content: '';
            position: absolute;
            left: -16px;
            top: -0.5px;
            width: 14px;
            height: 14px;
            background: url('~assets/img/collect.png')0 0/13px 13px no-repeat;
        }
    }
}

</style>